<template>
  <section class="subscribe-newsletter text-center py-20 bg-gray-100 max-w-7xl mx-auto">
    <div class="">
    <div class="grid grid-cols-1 md:grid-cols-6 gap-12 items-center justify-center mx-auto text-center md:text-left"> 
      <!-- Centered Grid and Text -->

      <!-- Image Section -->
      <div class="flex justify-center md:col-span-1"> 
        <img src="@/assets/newslatter.png" alt="Newsletter" class="" />
      </div>

      <!-- Title and Form Section -->
      <div class="md:col-span-5"> 
        <h2 class="text-2xl font-regular mb-2">SUBSCRIBE TO OUR NEWSLETTER</h2>
        <p class="text-md text-gray-600 mb-4">Get updates on upcoming auctions, news, and offers.</p>

        <!-- Form for email input -->
        <form @submit.prevent="handleSubmit" class="">
          <div class="border-2 border-gray-400 flex w-full">
            <input 
              type="email" 
              v-model="email" 
              placeholder="Email Address" 
              required 
              class="py-3 px-4 w-full border-0 focus:outline-none" 
            />
            <button 
              type="submit" 
              class="bg-gray-100 border-l border-gray-300 py-3 px-4 flex items-center justify-center"
              aria-label="Submit Email"
            >
              <svg 
                xmlns="http://www.w3.org/2000/svg" 
                class="h-6 w-6 text-gray-500" 
                fill="none" 
                viewBox="0 0 24 24" 
                stroke="currentColor" 
                aria-hidden="true"
              >
                <path 
                  stroke-linecap="round" 
                  stroke-linejoin="round" 
                  stroke-width="2" 
                  d="M13 7l5 5-5 5M6 12h12" 
                />
              </svg>
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>

    <p v-if="submitted" class="mt-4 text-green-500">Thank you for subscribing!</p>
  </section>
</template>

<script>
export default {
  name: 'SubscribeNewsletter',
  data() {
    return {
      email: '',
      submitted: false,
    };
  },
  methods: {
    handleSubmit() {
      console.log('Subscribed with email:', this.email);
      this.email = '';
      this.submitted = true;

      setTimeout(() => {
        this.submitted = false;
      }, 3000);
    },
  },
};
</script>

<style scoped>
.subscribe-newsletter {
  background-color: #f0e5e5; /* Light background */
}

form input {
  font-size: 16px;
}

form button {
  background-color: #f0e5e5; /* Light pink for button background */
}

form button:hover {
  background-color: #e0d3d3; /* Darker pink on hover */
}
</style>
